<template>
  <div class="admin">
    <div class="cont">
      <h2 style="text-align: left; color: #666; padding: 50px 47px">添加新的菜品</h2>
      <form>
        <div class="form-group row">
          <label for="food" class="col-sm-2 col-form-label">菜品名称</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" name="food" id="food">
          </div>
        </div>
        <div class="form-group row">
          <label for="describe" class="col-sm-2 col-form-label">描述(选填)</label>
          <div class="col-sm-10">
            <textarea rows="3" class="form-control" name="describe" id="describe"></textarea>
          </div>
        </div>
        <h4 style="text-align: left; padding-left: 47px; margin-top: 30px">选项1</h4>
        <div class="form-group row">
          <label for="big" class="col-sm-2 col-form-label">大小</label>
          <div class="col-sm-10">
            <input type="text" readonly class="form-control-plaintext" name="size" id="big" value="大份">
          </div>
        </div>
        <div class="form-group row">
          <label for="bigPrice" class="col-sm-2 col-form-label">价格(￥)</label>
          <div class="col-sm-10">
            <input type="number"  class="form-control" name="price" id="bigPrice">
          </div>
        </div>
        <h4 style="text-align: left; padding-left: 47px; margin-top: 30px">选项2</h4>
        <div class="form-group row">
          <label for="small" class="col-sm-2 col-form-label">大小</label>
          <div class="col-sm-10">
            <input type="text" readonly class="form-control-plaintext" id="small" name="size" value="小份">
          </div>
        </div>
        <div class="form-group row">
          <label for="smallPrice" class="col-sm-2 col-form-label">价格(￥)</label>
          <div class="col-sm-10">
            <input type="number"  class="form-control" name="price" id="smallPrice">
          </div>
        </div>
        <el-button @click="addFoods" type="success" plain>添加</el-button>
      </form>

    </div>
  </div>
</template>

<script>
import $ from "jquery";
import { Message } from "element-ui";
import axios from "axios";
import Cookies from "js-cookie";
export default {
  methods: {
    addFoods() {
      if (
        $("#food")[0].value &&
        $("#bigPrice")[0].value &&
        $("#smallPrice")[0].value
      ) {
        let formArray = $("form").serializeArray();
        let cookieData = Cookies.get();
        let auther;
        // 将表单数据改写成可提交的数据
        for (let key in cookieData) {
          if (cookieData[key] === "Admin") {
            auther = key;
          }
        }
        let data1 = {
          auther
        };
        let data2 = {
          auther
        };
        let authPrice;
        for (let i = 0; i < formArray.length; i++) {
          let name = formArray[i].name,
            val = formArray[i].value;
          if (name === "price") {
            if (!authPrice) {
              authPrice = val;
            } else {
              let max = authPrice > val ? authPrice : val;
              let min = authPrice > val ? val : authPrice;
              data2.price = max;
              data1.price = min;
            }
          } else {
            data2[formArray[i].name] = formArray[i].value;
            data1[formArray[i].name] = formArray[i].value;
            data2.size = "big";
            data1.size = "small";
          }
        }
        // 向后台提交数据
        axios.get("/foods.json").then(res => {
          let getData = res.data;
          let dataArray = [];
          for (let key in getData) {
            dataArray.push(getData[key]);
          }
          let authArray = dataArray.filter(item => {
            return item.food === formArray[0].value;
          });
          if (authArray.length > 0) {
            Message({
              showClose: true,
              message: "菜单中已经拥有该食品",
              type: "error"
            });
            $("#food").focus();
            $("#food").val("");
          } else {
            axios
              .post("/foods.json", data1)
              .then(() => {
                axios
                  .post("/foods.json", data2)
                  .then(() => {
                    $("input").val("");
                    $("textarea").val("");
                    Message({
                      showClose: true,
                      message: "菜品添加成功",
                      type: "success"
                    });
                    $("#food").focus();
                  })
                  .catch(err => {
                    console.log(err);
                  });
              })
              .catch(err => {
                console.log(err);
              });
          }
        });
      } else {
        Message({
          showClose: true,
          message: "请把商品信息填写完整！",
          type: "error"
        });
      }
    }
  },
  mounted() {
    $("#food").focus();
  }
};
</script>


<style lang="scss" scoped>
.admin {
  .cont {
    margin-top: 60px;
    padding: 0 47px 50px 0;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.5);

    .el-button {
      width: 300px;
      span {
        display: inline-block;
        width: 100%;
      }
    }
  }
}
</style>
