import Vue from "vue";
import Vuex from "vuex";
import Cookie from "./Cookie/index.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    msg: {
      log: "您还未登录"
    },
    suc: {
      logout: "已注销管理员身份"
    },
    err: {
      log: "您没有权限这么做",
      illegal: "非法操作~~"
    },
    isRouterAlive: true
  },
  mutations: {
    reload() {
      this.isRouterAlive = false;
      Vue.nextTick(() => {
        console.log(this.isRouterAlive);
        this.isRouterAlive = true;
      });
    }
  },
  modules: {
    ckData: Cookie
  }
});
