<template>
  <div class="register">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <h1>As the administrator</h1>
      <el-form-item
        prop="email"
        label="邮箱"
        :rules="rules.emails"
      >
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button ref="submit" type="primary" @click="submitForm('ruleForm')">注册</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import axios from "axios";
import { Message } from "element-ui";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if (value === this.checkEmail) {
        callback(new Error("邮箱已注册"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        email: "",
        pass: "",
        checkPass: ""
      },
      checkEmail: "",
      rules: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        emails: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          },
          { validator: validateEmail, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var user = {};
          user.email = this.ruleForm.email;
          user.pass = this.ruleForm.pass;
          axios.get("/user.json").then(res => {
            let users = [];
            for (let key in res.data) {
              users.push(res.data[key]);
            }
            let repUser = users.filter(item => {
              return item.email === user.email;
            });
            if (repUser.length > 0) {
              user = null;
            }
            if (user) {
              axios.post("/user.json", user).then(() => {
                this.$router.push("/login");
                Message({
                  message: "管理员注册成功",
                  type: "success"
                });
              });
            } else {
              this.checkEmail = this.ruleForm.email;
              document
                .querySelector(".register .el-input .el-input__inner")
                .focus();
              document
                .querySelector(".register .el-input .el-input__inner")
                .blur();
            }
          });
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>
