.register {
  .el-form {
    width: 700px;
    height: 500px;
    margin: 0 auto;
    margin-top: 150px;
    padding-top: 95px;
    padding-left: 15px;
    padding-right: 80px;
    box-shadow: 1px 1px 20px #ccc;
    background-color: #fffffe;

    h1 {
      padding-left: 80px;
    }
  }
}
</style>
