<template>
  <div id="app">
    <div class="bg-nav"></div>
    <div class="con">
      <el-menu router :default-active="activeIndex" class="el-menu-demo" mode="horizontal">
        <el-menu-item index="/"><div class="logo"></div></el-menu-item>
        <el-menu-item index="/">主页</el-menu-item>
        <template v-if="isDisabled">
          <el-menu-item index="/menu">菜单</el-menu-item>
        </template>
        <template v-else>
          <el-menu-item index="/_menu" :disabled="isDisabled">
            <i class="el-icon-setting"></i>
            <span slot="title">管理菜单</span>
          </el-menu-item>
        </template>
        <el-menu-item index="/admin" :disabled="isDisabled">
          <i class="el-icon-edit"></i>
          <span slot="title">管理</span>
        </el-menu-item>
        <el-menu-item index="/about">关于我们</el-menu-item>
        <template v-if="isDisabled">
          <el-menu-item index="/register">成为管理员</el-menu-item>
          <el-menu-item index="/login">登录</el-menu-item>
        </template>
        <template v-else>
          <el-menu-item index="">
            {{ userEmail }}
            <a href="javascript:" @click="removeUser">[注销]</a>
          </el-menu-item>
        </template>
      </el-menu>
      <transition name="fade-out">
        <router-view v-if="isRouterAlive"></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import Cookies from "js-cookie";
import { mapState } from "vuex";
import { Message } from "element-ui";

export default {
  name: "app",
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      activeIndex: "/munu",
      isRouterAlive: true
    };
  },
  computed: {
    ...mapState("ckData", ["isDisabled"]),
    userEmail() {
      return (function() {
        let logUser = Cookies.get();
        for (var key in logUser) {
          if (logUser[key] === "Admin") {
            return key;
          }
        }
      })();
    }
  },

  methods: {
    removeUser() {
      Cookies.remove(this.userEmail);
      this.$router.push("/login");
      Message({
        message: this.$store.state.suc.logout,
        type: "success"
      });
      this.$store.state.ckData.isDisabled = true;
    },
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(() => {
        this.isRouterAlive = true;
      });
    }
  },
  created() {
    let logUser = Cookies.get();
    for (var key in logUser) {
      if (logUser[key] === "Admin") {
        this.$store.state.ckData.isDisabled = false;
      } else {
        this.$store.state.ckData.isDisabled = true;
      }
    }
  }
};
</script>

