import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import Menu from "./views/Menu.vue";
import Admin from "./views/Admin.vue";
import Login from "./views/Login.vue";
import Register from "./views/Register.vue";
import _Menu from "./views/Menu_.vue";
import CallUs from "./views/About/CallUs.vue";
import Suggest from "./views/About/Suggest.vue";
import AboutUs from "./views/About/AboutUs.vue";

import Cookies from "js-cookie";
import store from "./store/index.js";
import { Message } from "element-ui";

Vue.component(Message);
Vue.use(Router);

var router = new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    // about
    {
      path: "/about",
      name: "about",
      component: About,
      redirect: "/about/call_us",
      children: [
        {
          path: "/about/call_us",
          component: CallUs
        },
        {
          path: "/about/suggest",
          component: Suggest
        },
        {
          path: "/about/about_us",
          component: AboutUs
        }
      ]
    },
    {
      path: "/menu",
      component: Menu
    },
    {
      path: "/_menu",
      component: _Menu
    },
    // admin
    {
      path: "/admin",
      components: {
        default: Admin
      },
      beforeEnter(to, from, next) {
        let logUser = Cookies.get();
        var isLog;
        for (var key in logUser) {
          if (logUser[key] === "Admin") {
            isLog = key;
          }
        }
        if (isLog) {
          next();
        } else {
          next("/login");
        }
      }
    },
    // login
    {
      path: "/login",
      component: Login,
      beforeEnter(to, from, next) {
        let logUser = Cookies.get();
        for (var key in logUser) {
          if (logUser[key] === "Admin") {
            Cookies.remove(key);
            store.state.ckData.isDisabled = true;

            Message({
              message: store.state.msg.log,
              type: "warning"
            });
          }
        }
        next();
      }
    },
    {
      path: "/register",
      component: Register
    }
  ]
});

router.beforeEach((to, from, next) => {
  let logUser = Cookies.get();
  for (var key in logUser) {
    if (logUser[key] === "Admin") {
      store.state.ckData.isDisabled = false;
    } else {
      store.state.ckData.isDisabled = true;
    }
  }
  next();
});
export default router;
