<template>
  <div class="admin-menu">
    <table class="table">
      <thead>
        <tr>
          <th scope="col">菜品</th>
          <th scope="col">份额</th>
          <th scope="col">价格</th>
          <th scope="col">上架</th>
          <th scope="col">操作</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item) in tableData">
          <tr>
            <th :class="item.options.id[0]+' change'" scope="row" rowspan="2">{{ item.name }}</th>
            <td :class="item.options.id[0]+' no-change'">{{ item.options.size[0] === "big" ? "大份" : "小份" }}</td>
            <td :class="item.options.id[0]+' change'">{{ item.options.price[0] }}</td>
            <td :class="item.options.id[0]+' no-change'" rowspan="2" style="line-height: 96px">{{ item.auther}}</td>
            <td :class="`${item.options.id[0]}-edit`" rowspan="2" style="line-height: 96px">
              <a :class="`${item.options.id[0]}-edit-a`" href="javascript:" @click="editData(item.options.id[0], `${item.options.id[0]}-edit`, item.options.id[1])">修改</a> |
              <a href="javascript:" @click="delData(item.options.id[0], item.options.id[1])">删除</a>
            </td>
          </tr>
          <tr>
            <td :class="item.options.id[0]+' no-change'">{{ item.options.size[1] === "big" ? "大份" : "小份" }}</td>
            <td :class="item.options.id[0]+' change'">{{ item.options.price[1] }}</td>
          </tr>
        </template>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
export default {
  inject: ["reload"],
  data() {
    return {
      isRouterAlive: true,
      tableData: [],
      testData: [
        {
          food: "干锅",
          price: 25,
          size: "big"
        },
        {
          food: "干锅",
          price: 12,
          size: "small"
        },
        {
          food: "烧鸭",
          price: 17,
          size: "big"
        },
        {
          food: "烧鸭",
          price: 7,
          size: "small"
        },
        {
          food: "火锅",
          price: 25,
          size: "big"
        },
        {
          food: "火锅",
          price: 12,
          size: "small"
        },
        {
          food: "烧及",
          price: 17,
          size: "big"
        },
        {
          food: "烧及",
          price: 7,
          size: "small"
        }
      ]
    };
  },
  methods: {
    delData(id0, id1) {
      axios.delete(`/foods/${id0}.json`).then(() => {
        axios.delete(`/foods/${id1}.json`).then(() => {
          this.reload();
        });
      });
    },
    editData(tarClass, editClass, id1) {
      console.log(tarClass, id1);
      let tarArray = $(`.${tarClass}.change`);
      let noTarArray = $(`.${tarClass}.no-change`);
      let submitArr = $(`.${tarClass}`);
      let editArray = $(`.${editClass}`);
      for (let i = 0, len = tarArray.length; i < len; i++) {
        tarArray[
          i
        ].innerHTML = `<input style="width:100px;text-align:center;margin:0 auto;" class="form-control form-control-sm" type="text" value="${tarArray[
          i
        ].innerText.replace(/\s+/g, "")}">`; // 清除空格
        noTarArray[
          i
        ].innerHTML = `<input style="width:100px;text-align:center;margin:0 auto;" type="text" readonly class="form-control-plaintext" id="staticEmail" value="${
          noTarArray[i].innerText
        }">`;
      }
      editArray[0].innerHTML = `
      <a href="javascript:" class="${tarClass}-submit">确认</a>
      `;
      $(`.${tarClass}-submit`).on("click", e => {
        let submitArrInput = submitArr.children("input");
        let data1 = {
            food: submitArrInput[0].value,
            size:
              submitArrInput[4].value.replace(/\s+/g, "") === "大份"
                ? "big"
                : "small",
            price: submitArrInput[5].value,
            auther: submitArrInput[3].value
          },
          data2 = {
            food: submitArrInput[0].value,
            size:
              submitArrInput[1].value.replace(/\s+/g, "") === "大份"
                ? "big"
                : "small",
            price: submitArrInput[2].value,
            auther: submitArrInput[3].value
          };
        axios.put(`/foods/${tarClass}.json`, data2).then(() => {
          axios.put(`/foods/${id1}.json`, data1).then(() => {
            this.reload();
          });
        });
      });
    }
  },
  created() {
    // 获取后台数据
    axios.get("/foods.json").then(res => {
      let dataArr = [];
      for (let key in res.data) {
        res.data[key]["_id"] = key;
        dataArr.push(res.data[key]);
      }
      let data = dataArr;
      for (let i = 1, len = data.length; i < len; i += 2) {
        let obj = {};
        let tarArr = data.filter(item => {
          return item.food === data[i].food;
        });
        obj.name = tarArr[0].food;
        obj.auther = tarArr[0].auther;
        obj.options = {
          size: [tarArr[0].size, tarArr[1].size],
          price: [tarArr[0].price, tarArr[1].price],
          id: [tarArr[0]["_id"], tarArr[1]["_id"]]
        };
        this.tableData.unshift(obj);
      }
    });
  }
};
</script>


<style lang="scss" scoped>
.admin-menu {
  table {
    background-color: rgba(0, 0, 0, 0.3);
    color: #eee;
    tr,
    td,
    th {
      border: transparent;
    }
    thead {
      th {
        &:first-child {
          border-right: solid 1px rgba(225, 225, 225, 0.5);
        }
      }
    }
    tbody {
      tr {
        th {
          padding: 0;
          line-height: 96px;
          border-right: solid 1px rgba(225, 225, 225, 0.5);
        }
      }
    }
  }
}
</style>
