<template>
  <div class="menu">
    <el-table
      :data="nowData"
      style="width: 100%">
      <el-table-column
        prop="food"
        label="菜品">
      </el-table-column>
      <el-table-column
        prop="price"
        label="价格">
      </el-table-column>
      <el-table-column
        label="选项">
        <template slot-scope="scope">
          <select class="custom-select" :class="'sel'+scope.$index" @change="selChange(scope.$index, scope.row.food)">
            <option value="big">大份</option>
            <option value="small">小份</option>
          </select>
        </template>
      </el-table-column>
      <el-table-column
        label="加入">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="addFood(scope.$index, scope.row)">+</el-button>
        </template>
      </el-table-column>
    </el-table>
    <table v-if="shoppingData.length > 0" class="shopping table">
      <thead>
        <tr>
          <th scope="col">数量</th>
          <th scope="col">品种</th>
          <th scope="col">大小</th>
          <th scope="col">价格</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in shoppingData" :key="index">
          <td>
            <div class="com-list">
              <span class="subtr" @click="plusFoodNum(-1, index)"> - </span>
              <div class="number" ref="num">{{item.shoppingNum}}</div>
              <span class="plus" @click="plusFoodNum(1, index)"> + </span>
            </div>
          </td>
          <td>{{ item.food }}</td>
          <td>{{ item.size === "big" ? "大份" : "小份" }}</td>
          <td class="pad">{{ Math.floor(item.price * 10) / 10 }}</td>
        </tr>
        <tr>
          <td colspan="4">
            <h4>应付： {{ totalPrice }}</h4>
          </td>
        </tr>
        <tr>
          <td colspan="4">
            <el-button plain type="primary" style="width: 100%">点餐</el-button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="shopping" v-else>
      <h1 style="margin-top: 130px; color: #aaa">购物车</h1>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "menuOption",
  data() {
    return {
      watchPrice: 0,
      totalPrice: 0,
      value: "大份",
      padNum: 0,
      nowData: [],
      shoppingData: [],
      tableData: []
    };
  },
  methods: {
    // size选择
    selChange(index, tar) {
      let changedSize = document.querySelector(`select.${"sel" + index}`).value;
      let newSize = this.tableData.filter(item => {
        return item.food === tar && item.size === changedSize;
      });
      this.nowData.splice(index, 1, newSize[0]);
    },
    // 添加食品
    addFood(index, tarRow) {
      let isHaveThisFoodIndex = this.shoppingData.findIndex(item => {
        return item.food === tarRow.food && item.size === tarRow.size;
      });
      if (isHaveThisFoodIndex === -1) {
        // 实现深拷贝，以防止蝴蝶效应
        let shoppingRow = JSON.parse(JSON.stringify(tarRow));
        shoppingRow.shoppingNum = 1;
        shoppingRow.unitPrice = tarRow.price;
        this.shoppingData.push(shoppingRow);
      } else {
        this.plusFoodNum(1, isHaveThisFoodIndex);
      }
      this.watchPrice += 1;
    },
    // 改变食品数量
    plusFoodNum(judge, index) {
      let shoppingRow = this.shoppingData[index];
      switch (judge) {
        case -1:
          if (shoppingRow.shoppingNum > 0) {
            shoppingRow.shoppingNum--;
            if (shoppingRow.shoppingNum <= 0) {
              this.shoppingData.splice(index, 1);
            }
          }
          break;
        case 1:
          shoppingRow.shoppingNum++;
          break;
      }
      shoppingRow.price = shoppingRow.unitPrice * shoppingRow.shoppingNum;
      this.watchPrice += 1;
    }
  },
  beforeCreate() {
    axios.get("/foods.json").then(res => {
      for (let key in res.data) {
        this.tableData.unshift(res.data[key]);
      }
      console.log(this.tableData);
      this.nowData = this.tableData.filter(item => {
        return item.size === "big";
      });
    });
  },
  created() {},
  updated() {
    let classPads = document.querySelectorAll(".pad");
    let sign = 0;
    for (let i = 0, len = classPads.length; i < len; i++) {
      sign += Number(classPads[i].innerHTML);
    }
    this.totalPrice = sign;
  }
};
</script>

