<template>
  <div class="login">
    <div class="cont">
      <h1>Administrator Login</h1>
      <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
        <el-form-item
          prop="email"
          label="邮箱"
          :rules="[
            { required: true, message: '请输入邮箱地址', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
          ]"
        >
          <el-input v-model="dynamicValidateForm.email"></el-input>
        </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input type="password" v-model="dynamicValidateForm.pass" autocomplete="off"></el-input>
      </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('dynamicValidateForm')">登录</el-button>
          <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
          <router-link to="/register" style="float: right;margin-right: 20px; margin-top: 5px; font-size: 16px">注册账号</router-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import Cookies from "js-cookie";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("密码不能为空"));
      }
    };
    return {
      dynamicValidateForm: {
        email: "",
        pass: ""
      },
      rules: { pass: [{ validator: validatePass, trigger: "blur" }] }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios.get("/user.json").then(res => {
            let users = [];
            for (let key in res.data) {
              users.push(res.data[key]);
            }
            let sucUser = users.filter(item => {
              return (
                item.email === this.dynamicValidateForm.email &&
                item.pass === this.dynamicValidateForm.pass
              );
            });
            if (sucUser.length > 0) {
              Cookies.set(this.dynamicValidateForm.email, "Admin");
              this.$router.push("/");
              window.location.reload();
            }
          });
        } else {
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

